body {
  color: #757e63;
  background-color: #1d1b1b;
  font-family: helvetica neue;
}

@media (width <= 768px) {
  .mobile {
    display: block;
  }

  .desktop {
    display: none;
  }

  .mobile-container {
    grid-template: "a"
                   "b"
                   "c" 1fr
                   / 100%;
    min-height: 98vh;
    display: grid;
  }

  .header {
    flex-direction: row;
    justify-content: end;
    align-items: center;
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: 200;
    display: flex;
  }

  .bda-logo {
    align-items: center;
    display: flex;
  }

  .menu-image {
    width: 50px;
    height: 50px;
  }

  .bda-title {
    text-align: center;
    flex-grow: 1;
  }

  .bda-hamburger {
    justify-content: flex-end;
    margin-left: auto;
  }

  .mobile-main {
    margin-top: 20px;
  }

  .mob-a {
    grid-area: a;
  }

  .mobile-main-title {
    text-align: right;
    font-size: 20px;
    font-weight: 200;
  }

  .mobile-sub-title {
    text-align: right;
    font-size: 15px;
    font-weight: 200;
  }

  .mob-b {
    grid-area: b;
  }

  .mob-c {
    grid-area: c;
  }
}

@media (width >= 769px) {
  .mobile {
    display: none;
  }

  .desktop {
    display: block;
  }

  .desktop-container {
    grid-template: "b c c c c" 1fr
    / 20% 20% 20% 20% 20%;
    min-height: 98vh;
    display: grid;
  }

  .header {
    flex-direction: row;
    align-items: center;
    margin-bottom: 20px;
    font-size: 25px;
    font-weight: 200;
    display: flex;
  }

  .desk-b {
    grid-area: b;
  }

  .desk-c {
    grid-area: c;
  }

  nav {
    display: none;
  }

  .menu-container {
    flex-direction: column;
    align-items: center;
    margin-left: 5%;
    display: flex;
  }

  #menu-box {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 15px;
  }

  .menu-image {
    width: 100px;
    height: 100px;
  }

  .page-header {
    flex-direction: column;
    align-items: flex-end;
    display: flex;
  }

  h1 {
    margin-bottom: 0%;
    font-size: 50px;
    font-weight: 200;
  }

  h2 {
    margin-top: 0%;
    font-weight: 200;
  }

  ul {
    text-align: left;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  ul li {
    margin-bottom: 15px;
    font-size: 20px;
    font-weight: 200;
  }

  li:hover {
    color: #fff;
  }

  li:first-child {
    margin-bottom: 30px;
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  #current {
    text-decoration-line: underline;
    text-decoration-thickness: 1px;
  }

  #top {
    font-size: 30px;
  }
}

@media (width >= 768px) {
  svg {
    color: #e4e9dd;
  }

  #beckham-container {
    justify-content: center;
    margin-left: 5%;
  }

  .visualisation-container {
    flex-direction: row;
    align-items: flex-start;
    margin-top: 20px;
    display: flex;
  }

  #desktop-club-filter {
    color: #757e63;
    -webkit-font-smoothing: antialiased;
    background-color: #1d1b1b;
    border-color: #757e63;
    border-radius: 4px;
    padding: 2.5px 10px 2.5px 0;
    font-family: Open Sans Light, sans-serif;
    font-size: 20px;
    font-weight: 100;
  }

  .desktop-legend-tooltip {
    margin-top: 40px;
    margin-left: 30px;
  }

  .mobile-legend-container {
    flex-flow: wrap;
    gap: 10px;
    padding: 0 10px 10px 0;
    display: flex;
  }

  .desktop-legend-item {
    align-items: center;
    margin-bottom: 5px;
    font-size: 25px;
    font-weight: 200;
    display: flex;
  }

  .desktop-legend-color {
    border: 1px solid #000;
    width: 25px;
    height: 25px;
    margin-right: 15px;
  }

  .desktop-tooltip {
    pointer-events: none;
    opacity: 0;
    background-color: #1d1b1b;
    border-radius: 4px;
    margin-top: 15px;
    font-size: 25px;
    font-weight: 200;
    transition: opacity .3s ease-in-out;
  }
}

svg {
  color: #e4e9dd;
}

image {
  border: 1px solid #f43307;
}

select {
  font-weight: 100;
}

.axis path {
  stroke: none;
}

.axis line {
  stroke: red;
}

.axis .tick {
  display: none;
}

.axis .domain {
  stroke: none;
}

.text {
  color: #2256f1;
}

.axis text {
  display: none;
}

.pitch {
  transition: opacity .3s ease-in-out;
}

.goal-path {
  transition: opacity .1s ease-in-out;
}
